import React, { PureComponent } from 'react';

export default class Home extends PureComponent {
  render() {
    return (
      <div className='App'>
        <h1>Velkomin</h1>
        <h2>
          Alicia er vefkerfi sem leyfir innsendingu verkefna nemenda og yfirfer
          kennara  heimaverkefnum  formi frumka.
        </h2>
        <p>
          Aal inntak verkefnisins er a leyfa skilvirkari og einfaldari
          yfirfer  verkefnum en nverandi kerfi leyfa.
        </p>
        <p>
          a er gert me samanburi  nemendalausnum og snislausn og er
          lausnunum raa upp me tilliti til fjarlg eirra fr snislausn.
        </p>
        <a href="https://github.com/steina1989/Alicia">Verkefni  Github</a>
      </div>
    );
  }
}



